.main-footer {
  background: #011C40;
}

.main-footer__container {
  display: flex;
  padding-top: 50px;
  padding-bottom: 50px;

  @include vp-1023 {
    padding-top: 48px;
    padding-right: 42px;
  }

  @include vp-767 {
    padding-top: 40px;
    flex-direction: column;
  }
}

.main-footer__logo {
  margin-right: 40px;
  fill: #f9fbfd;

  @include vp-1023 {
    width: 38px;
    height: 20px;
    margin-right: 24px;
    margin-top: 2px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
    margin-top: 0;
    margin-bottom: 23px;
  }
}
